<!--  -->
<template>
  <div class="chat">
    <div class="top">
      <div class="back" @click="back"></div>
      
    </div>
    <div class="content" ref="content">
      <div class="list-item" v-for="(item, index) in chatList" :key="index">
        <div class="msg-item-left msg-item" v-if="item.moduleid">
          <h5>{{item.time}}</h5>
          <div class="main">
            <div class="avatar">
              <img :src="$route.params.icon" alt="">
            </div>
            <div class="msg-content">
              <h6>      </h6>
              <p>{{item.sendmess}}</p>
            </div>
          </div>
        </div>
        <div class="msg-item-right msg-item" v-else>
          <h5>{{item.time}}</h5>
          <div class="main">
            <div class="avatar">
              <img src="../../assets/images/user@2x.png" alt="">
            </div>
            <div class="msg-content">
              <p>{{item.replymess}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="input-bar">
      <div class="textarea" ref="textarea" contenteditable="true"></div>
      <div class="submit" @click="submit"></div>
      <!-- <input type="text" placeholder=""> -->
    </div>
  </div>
</template>

<script>
import moment from 'moment';
// import { getSocket } from '../../assets/js/socket';
import io from '../../assets/js/socket.io.min.js';

export default {
  name: "MessageChat",
  data() {
    return {
      socket: null,
      wsData: {}, //  websocket 
      form: { // 
        name: '',
        age: ''
      },
      chatList: []
    };
  },
  created() {
    // this.getSocketData();
  },
  mounted() {
    this.key = this.$route.params.moduleid + '&' + 100;
    this.chatList = JSON.parse(localStorage.getItem(this.key) || '[]');
    this.socket = io.connect('http://172.20.10.3:5001/chat_page_conn', {transports: ['websocket']});
    setTimeout(() => {
      this.sendSocketData('');
    }, 1000);
    this.socket.on('server_response', function(res) {
      console.log(res.data);
    });
    if (this.chatList.length === 0) {
      this.getChatList(this.$route.params.moduleid);
    } else {
      this.scrollToBottom();
    }
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    scrollToBottom() {
      setTimeout(() => {
        window.scrollTo({
          top: this.$refs.content.scrollHeight
        });
      });
    },
    getChatList(moduleid) {
      this.$axios.get('/mobile/xxtx/py/chat-page', {
          params: {
            // "aamid": this.$store.state.userInfoObj.aamid,
            "aamid": "100",
            moduleid
          }
        }).then(res => {
        	if (res.status === 200 && res.data.message === 'OK') {
            this.chatList = res.data.data || [];
            this.scrollToBottom();
        	} else {

        	}
        }).catch(err => {
        });
    },
    // 
    getSocketData() {
      console.log('get');
      this.socket.on('server_response', function(res) {
        console.log(res.data);
      });
    },
    // 
    sendSocketData(text) {
      if (text === '') return;
      const time = moment().format("YYYY-MM-DD HH:mm:ss");
      this.chatList.push({
        aamid: "100",
        replymess: text,
        time
      });
      this.socket.on('connect', () => {
        this.socket.emit('receive', {
          "aamid": 100,
          "moduleid": this.$route.params.moduleid,
          "modulename": this.$route.params.modulename,
          "msg": text,
          "time": time
        });
      });
      this.scrollToBottom();
    },
    submit() {
      const value = this.$refs.textarea.innerText.trim();
      if (value) {
        this.$refs.textarea.innerHTML = '';
        this.sendSocketData(value);
      }
    }
  },
  beforeDestroy() {
    localStorage.setItem(this.key, JSON.stringify(this.chatList));
    // this.wsData.close(); //  websocket
  }
};
</script>
<style lang='less' scoped>
/* @import url(); css */
.chat{
  height: 100%;
}
.top{
  width: 100%;
  height: 0.5rem;
  border-bottom: 1px solid #EAEAEA;
  line-height: 0.5rem;
  text-align: center;
  background: #fff;
  font-size: 0.16rem;
  font-weight: 500;
  color: #232323;
  position: fixed;
  .back{
    width: 0.4rem;
    position: absolute;
    height: 100%;
    background: url('../../assets/images/icon_return@2x.png') no-repeat center center;
    background-size: 0.2rem 0.2rem;
  }
}
.content{
  padding: 0.5rem 0;
  min-height: 100%;
  background: #eeeeee;
  box-sizing: border-box;
  .msg-item{
    overflow: hidden;
    margin-top: 0.2rem;
    h5{
      font-size: 0.14rem;
      text-align: center;
      color: #A6A6A6;
      margin-bottom: 0.16rem;
    }
    .main{
      overflow: hidden;
      .avatar{
        float: left;
        width: 0.4rem;
        height: 0.4rem;
        background: #fff;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        overflow: hidden;
        img{
          width: 100%;
        }
      }
      .msg-content{
        float: left;
        max-width: 2.5rem;
        margin: 0 0.1rem;
        padding: 0.1rem 0.12rem;
        font-size: 0.16rem;
        color: #3A3A3A;
        border-radius: 4px;
        line-height: 0.22rem;
        box-sizing: border-box;
        h6{
          text-align: center;
        }
      }
    }
  }
  .msg-item-left{
    padding-left: 0.12rem;
    .msg-content{
      background: #fff;
    }
  }
  .msg-item-right{
    padding-right: 0.12rem;
    .main{
      .avatar{
        float: right;
        img{
          height: 90%;
          width: auto;
        }
      }
      .msg-content{
        float: right;
        background: #66DAD6;
      }
    }
  }
}
.input-bar{
  min-height: 0.5rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #F1F1F1;
  box-shadow: 0 0 4px 0 rgba(0,0,0,0.11);
  padding: 0.07rem 0.1rem;
  box-sizing: border-box;
  .textarea{
    float: left;
    outline: none;
    min-height: 0.2rem;
    line-height: 0.2rem;
    font-size: 0.14rem;
    padding: 0.08rem 0.15rem;
    padding-right: 0.42rem;
    border-radius: 0.18rem;
    background: #fff url('../../assets/images/icon_keyboard@2x.png') no-repeat 2.6rem 0.02rem;
    background-size: 0.32rem 0.32rem;
    width: 3rem;
    box-sizing: border-box;
    -webkit-user-select:text
  }
  .textarea:empty:before{ 
    content: ''; 
    color: gray; 
  } 
  .textarea:focus:before{
    content:none;
  }
  .submit{
    float: right;
    margin-top: 0.04rem;
    width: 0.5rem;
    height: 0.28rem;
    font-size: 0.14rem;
    color: #ffffff;
    background: #45a4b1;
    line-height: 0.28rem;
    text-align: center;
  }
}
</style>